$(document).ready(function () {
  const users = new Swiper(".js-users-slider", {
    slidesPerView: "auto",

    spaceBetween: 24,
    speed: 1000,
    loop: true,
    // autoplay: {
    //     delay: 5000,
    //     speed: 1000,
    //     disableOnInteraction: false
    // },
    navigation: {
      nextEl: ".swiper-next",
      prevEl: ".swiper-prev",
    },
  });

  const usersContent = new Swiper(".js-users-content-slider", {
    slidesPerView: "auto",

    spaceBetween: 24,
    speed: 1000,
    loop: true,
    // autoplay: {
    //     delay: 5000,
    //     speed: 1000,
    //     disableOnInteraction: false
    // },

    navigation: {
      nextEl: ".js-arrow-left",
      prevEl: ".js-arrow-right",
    },
  });

  usersContent.controller.control = users;
  users.controller.control = usersContent;

  $(".un-customer .js-cuurent-slide").text(usersContent.realIndex + 1);

  $(".un-customer .js-total-slides").text(($(".js-users-slider .swiper-slide").length)/3)
  usersContent.on("slideChange", function () {
    $(".un-customer .js-cuurent-slide").text(usersContent.realIndex + 1);
  });
});
